import {tasksReducer} from '../features/Todolists/todolist/tasks-reducer';
import {todoListsReducer} from "../features/Todolists/todolist/todoLists-reducer";
import {combineReducers} from 'redux';
import thunkMiddleware from "redux-thunk";
import {appReducer} from "./app-reducer";
import {authReducer} from '../features/Todolists/Authorization/authReducer';
import {configureStore} from "@reduxjs/toolkit";

//  reducer-   combineReducers,
//      -
const rootReducer = combineReducers({
    tasks: tasksReducer,
    todoLists: todoListsReducer,
    app: appReducer,
    auth: authReducer
})
//   store
//export const store = createStore(rootReducer, applyMiddleware(thunkMiddleware));
export type RootReducerType = typeof rootReducer;
export const store = configureStore({
    reducer: rootReducer,
    middleware: (getDefaultMiddleware) =>
        getDefaultMiddleware().prepend(thunkMiddleware)
})
//      
export type AppRootStateType = ReturnType<RootReducerType>

//  ,         store   
// @ts-ignore
window.store = store;

export type AppDispatchType = typeof store.dispatch
